@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
/* @import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap'); */

body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  font-family: "Raleway", sans-serif;
  /* font-family: 'Montserrat', sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.StakeMenu {
  background-color: rgb(33, 36, 41);
  width: 75%;
  max-width: 420px;
  padding: 1rem;
  border-radius: 2rem;
  box-shadow: rgba(0, 0, 0, 0.01) 0px 0px 1px, rgba(0, 0, 0, 0.04) 0px 4px 8px,
    rgba(0, 0, 0, 0.04) 0px 16px 24px, rgba(0, 0, 0, 0.01) 0px 24px 32px;
}

.StakeMenu-Input-Container {
  border-radius: 1.5rem;
  border: 1px solid rgb(44, 47, 54);
  background-color: rgb(33, 36, 41);
  margin-bottom: 1rem;
}

.StakeMenu-Input-Label {
  line-height: 1rem;
  padding: 0.75rem 1rem 0px;
  text-align: left;
}

.StakeMenu-Input {
  display: flex;
  flex-direction: row;
}

.StakeMenu-Input-Input {
  color: rgb(195, 197, 203);
  font-weight: 500;
  outline: none;
  border: none;
  background-color: rgb(33, 36, 41);
  font-size: 1.5rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-appearance: textfield;
     -moz-appearance: textfield;
          appearance: textfield;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  margin: 0.75rem 0.75rem 0.75rem 1rem;
  box-sizing: border-box;
  min-width: 1rem;
}

.StakeMenu-Input-Token {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-weight: 500;
  font-size: 1.5rem;
  padding-right: 1rem;
}

.StakeMenu-Logo {
  max-height: 2rem;
  margin-right: 0.5rem;
}

.StakeMenu-Button-Container {
  padding: 1rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.StakeMenu-Button {
  min-width: 60%;
  position: relative;
  font-weight: 500;
  font-size: 1rem;
  color: rgb(195, 197, 203);
  border-radius: 1.5rem;
  padding: 1rem;
  border: 2px solid #453B88;
  background-color: #453B88;
  outline: none;
}

.StakeMenu-Button.Left {
  left: 8%;
}

.StakeMenu-Button.Right {
  right: 8%;
}

.Active {
  z-index: 10;
}

.Disabled {
  background-color: rgb(33, 36, 41);
  z-index: 0;
}

.StakeMenu-Current {
  padding: 1rem;
  padding-left: 0.5rem;
  text-align: left;
}

.StakeMenu-Send {
  margin-top: 1rem;
  min-width: 100%;
  border-radius: 1rem;
  padding: 1rem;
  font-weight: 500;
  font-size: 1rem;
  background-color: #453B88;
  outline: none;
  border: none;
  color: rgb(195, 197, 203);
}

.StakeMenu-Send:disabled {
  opacity: 0.5;
}

.StakeMenu-Send:active {
  background-color: #292351;
}

.Nav {
  min-height: 3rem;
  width: 100vw;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.Nav-Button {
  width: 12rem;
  font-weight: 500;
  font-size: 1rem;
  color: rgb(195, 197, 203);
  border-radius: 1.5rem;
  padding: 0.5rem;
  border: 2px solid #453b88;
  background-color: #453b88;
  outline: none;
  margin: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Nav-Button:active {
  background-color: #292351;
}

.Nav-Button-Dot {
  min-height: 10px;
  min-width: 10px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  margin-right: 1rem;
  margin-left: 0.5rem;
}

.Nav-Button-Text {
  width: 85%;
  overflow: hidden;
  text-overflow: ellipsis;
}

.Red {
  background-color: #7f0000;
}

.Alert-Container {
  width: 75%;
  max-width: 420px;
  position: absolute;
  padding-bottom: 1rem;
  bottom: 0;
  color: rgb(195, 197, 203);
}

.Alert-Alert {
  padding: 0.7rem;
  border-radius: 1rem;
  background-color: rgb(33, 36, 41);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-right: 1rem;
  padding-left: 1.5rem;
}

.Warning {
  border: 2px solid #cf6679;
}

.Success {
  border: 2px solid #453b88;
}

.Alert-Alert > a {
  color: rgb(195, 197, 203);
}

.Alert-Alert > a:visited {
  color: rgb(195, 197, 203);
}

.close {
  cursor: pointer;
  font-size: 2rem;
  height: 1rem;
  width: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  bottom: 0.2rem;
}

.App {
  text-align: center;
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: radial-gradient(50% 50% at 50% 50%, rgba(69, 59, 136, 0.2) 0%, rgba(33, 36, 41, 0) 100%);
  background-color: rgb(44, 47, 54);
  font-weight: 500;
  font-size: 1rem;
  color: rgb(195, 197, 203);
}

